@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.btn-brown{
    font-size: 30px;
    font-weight: 600;
    color: $brown;
    width: 469px;
    height: 121px;
    background: linear-gradient(86.25deg, #FFBD3E 0%, #FFDB5B 21.84%, #FFBD3E 47.28%, #FFAA29 72.31%, #FFBD3E 99.28%);
    mix-blend-mode: normal;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 0px 17px #FFFFFF;
    border-radius: 10px;
    &:hover{
        color: darken($brown,10%);
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05), inset 0px 0px 17px #FFFFFF;
    }
}


@media screen and (max-width: 1200px){
    .btn-brown{
        width: 328px;
        max-width: 100%;
        height: 75px;
    }
}
