.header{
  padding: 11% 0 3.7%;
  position: relative;
  background-image: url(../img/1.webp);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  &__title{
    font-size: 240px;
    text-transform: uppercase;
    color: #111111;
    font-family: $font2;
    font-weight: 400;
  }
  &__desc{
    display: flex;
    align-items: center;
    margin-top: 26px;
    font-size: 68px;
    line-height: 114%;
    color: #ff5f79;
    font-family: $font3;
    letter-spacing: 0.015em;
    img{
      margin-right: 21px;
      flex-shrink: 0;
    }
  }
  &__girl{
    position: absolute;
    bottom: -8px;
    right: 0;
    width: 1344px;
    max-width: 70%;
  }
  &__title,
  &__desc,
  &__btn{
    position: relative;
    z-index: 2;
  }
  &__btn{
    margin-top: 25px;
  }
}


@media screen and (max-width: 1200px){
  .header{
    &__title{
      font-size: 180px;
    }
    &__desc{
      font-size: 52px;
      img{
        width: 37px;
      }
    }
    &__btn{
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    overflow: hidden;
    padding: 40px 0;
    text-align: center;
    &__title{
      font-size: 100px;
    }
    &__desc{
      margin-top: 5px;
      font-size: 40px;
      justify-content: center;
      img{
        width: 28px;
        margin-right: 8px;
      }
    }
    &__girl{
      display: block;
      position: static;
      width: 600px;
      margin: 0 auto;
      max-width: 100%;
    }
    &__btn{
      transform: translateY(-80px);
      font-size: 24px;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    background-position: unset;
    &__girl{
      width: calc(100% + 40px);
      margin-left: -20px;
      max-width: unset;
    }
  }
}
