.health{
  padding: 65px 0 23px;
  &__title{
    font-size: 90px;
    line-height: 86%;
    text-transform: uppercase;
    color: #111111;
    font-family: $font2;
    font-weight: 400;
    text-align: center;
  }
  &__desc{
    text-align: center;
    margin-top: 20px;
    font-size: 40px;
    color: #ff5f79;
    font-family: $font3;
    letter-spacing: 0.015em;
  }
  &__inner{
    margin-top: 44px;
    padding: 36px 39px 41px;
    background-image: url(../img/health-bg.webp);
    background-size: cover;
    background-position: right top;
    color: #fff;
    border-radius: 10px;
  }
  &__list{
    max-width: 800px;
    li{
      font-size: 20px;
      line-height: 120%;
      letter-spacing: 0.015em;
      padding-left: 43px;
      position: relative;
      &:not(:last-child){
        margin-bottom: 30px;
      }
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: 13px;
        height: 13px;
        background: #FFD367;
        border-radius: 50%;
      }
    }
  }
}

.why{
  padding: 47px 0 0;
  position: relative;
  &__decor{
    position: absolute;
    left: 0;
    bottom: -200px;
    pointer-events: none;
  }
  &__title{
    text-align: center;
    letter-spacing: 0.015em;
  }
  &__grid{
    margin-top: 27px;
    display: grid;
    grid-template-columns: 20% 60.7% 20%;
  }
  &__img{
    img{
      width: 100%;
    }
    &-mob{
      display: none;
    }
  }
  @media screen and (min-width: 768px){
    &__col{
      display: flex;
      flex-direction: column;
      justify-content: center;
      &:nth-child(1){
        text-align: right;
        width: 160%;
        margin-right: -60%;
        padding-bottom: 25%;
        .why__item{
          &:not(:last-child){
            margin-bottom: 16%;
          }
          &:nth-child(2){
            padding-right: 35px;
          }
          &:nth-child(3){
            padding-right: 15px;
          }
        }
      }
      &:nth-child(3){
        width: 163%;
        margin-left: -63%;
        padding-bottom: 25%;
        .why__item{
          &:not(:last-child){
            margin-bottom: 14%;
          }
          &:nth-child(2){
            padding-left: 25px;
          }
          &:nth-child(3){
            padding-left: 30px;
          }
        }
      }
    }
  }
  &__item{
    font-size: 17px;
    line-height: 20px;
    letter-spacing: 0.015em;
    color: #696969;
    strong{
      font-weight: 700;
    }
  }
}

.who{
  &__inner{
    color: #fff;
    border-radius: 10px;
    background-image: url(../img/who.webp);
    background-size: cover;
    background-position: center;
    text-align: center;
    padding: 14px 25px 25px;
  }
  &__grid{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 20px;
  }
  &__item{
    padding: 10px 20px;
    width: 100%;
    max-width: 342px;
  }
  &__img{
    display: flex;
    justify-content: center;
    height: 161px;
    img{
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
    }
  }
  &__subtitle{
    font-size: 17px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.015em;
    font-weight: 400;
    margin-top: 18px;
    b{
      font-weight: 700;
    }
  }
}

.course{
  padding: 43px 0 50px;
  &__inner{
    position: relative;
    padding: 0 0 160px 30%;
  }
  &__img{
    position: absolute;
    top: -18px;
    left: 0;
    display: block;
    width: 56.41%;
  }
  &__items{
    margin-top: 13px;
    margin-left: 10%;
  }
  &__item{
    display: flex;
    &:not(:last-child){
      margin-bottom: 70px;
    }
    &-icon{
      display: flex;
      justify-content: flex-end;
      width: 100px;
      flex-shrink: 0;
      margin-right: 20px;
      img{
        display: block;
        max-width: 100%;
        height: auto;
      }
    }
    &-title{
      font-size: 20px;
      line-height: 24px;
      letter-spacing: 0.015em;
      color: #696969;
      b{
        font-weight: 700;
      }
    }
  }
}

.require{
  display: grid;
  grid-template-columns: auto 596px;
  background: rgba(237, 237, 237, 0.8);
  border-radius: 10px;
  position: relative;
  z-index: 2;
  &__left{
    min-height: 170px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 30px;
    position: relative;
  }
  &__right{
    background-image: url(../img/require-bg.webp);
    background-size: cover;
    background-position: center;
    padding: 17px 47px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
  }
  &__man{
    position: absolute;
    left: 0;
    bottom: 0;
    max-width: 167px;
    max-height: 100%;
  }
  &__item{
    display: flex;
    align-items: flex-end;
    padding-left: 57px;
    position: relative;
  }
  &__num{
    position: absolute;
    bottom: 0;
    left: 0;
    flex-shrink: 0;
    font-size: 160px;
    line-height: 100%;
    font-family: $font2;
    letter-spacing: 0.015em;
    background: linear-gradient(360deg, #FFFFFF 49.35%, rgba(255, 255, 255, 0) 216.88%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  &__text{
    font-size: 20px;
    line-height: 24px;
    color: #fff;
    min-height: 72px;
  }
}

.comfort{
  text-align: center;
  padding: 50px 0 14px;
  &__title{
    font-family: $font2;
    font-size: 120px;
    letter-spacing: 0.015em;
    background: linear-gradient(355.98deg, #000000 26.57%, rgba(0, 0, 0, 0) 130.91%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  &__subtitle{
    color: #FF5F79;
  }
  &__title,
  &__subtitle,
  &__try,
  &__btn{
    position: relative;
    z-index: 2;
  }
  &__img{
    display: block;
    width: 100%;
    margin-top: -70px;
  }
  &__try{
    margin-top: -200px;
    font-family: $font3;
    font-size: 35px;
    line-height: 34px;
    letter-spacing: 0.015em;
    color: #686868;
  }
  &__btn{
    margin: 14px auto 0;
  }
}

.review{
  padding: 14px 0 30px;
  &__title{
    text-align: center;
  }
  &__items{
    margin-top: 31px;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 20px;
  }
}

.faq{
  padding: 30px 0 47px;
  &__title{
    text-align: center;
  }
  &__items{
    background-image: url(../img/faq-bg.webp);
    background-size: 100% auto;
    background-position: center top;
    padding: 177px 0 34px;
    margin-top: -29px;
  }
}

.conscious{
  padding: 47px 0 17px;
  &__title{
    text-align: center;
    font-family: $font2;
    font-size: 240px;
    letter-spacing: 0.015em;
    background: linear-gradient(353.7deg, #000000 27.37%, rgba(0, 0, 0, 0) 122.26%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  &__desc{
    color: #FF5F79;
    font-size: 60px;
    line-height: 58px;
    text-align: center;
    margin-top: 15px;
  }
  &__img{
    width: 100%;
    display: block;
    margin-top: 21px;
  }
}

.buy{
  margin-top: -21px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%), linear-gradient(180deg, #2AB45A 0%, #78C252 100%);
  border-radius: 10px;
  padding: 40px 28px;
  position: relative;
  z-index: 2;
  display: flex;
  overflow: hidden;
  &:before,
  &:after{
    content: '';
    position: absolute;
    border-radius: 50%;
  }
  &:before{
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.17);
    width: 526px;
    height: 526px;
    top: -80px;
    right: -161px;
  }
  &:after{
    top: -47px;
    right: -202px;
    width: 498px;
    height: 498px;
    background: linear-gradient(271.05deg, rgba(248, 72, 72, 0.94) 13.66%, rgba(255, 124, 144, 0.99) 89%);
  }
  &__price{
    flex-shrink: 0;
  }
  &__sum{
    padding: 25px 30px;
    background: #FFFFFF;
    border-radius: 5px;
    flex-shrink: 0;
    position: relative;
    margin-right: 37px;
    &-num{
      font-family: $font2;
      font-weight: 400;
      font-size: 240px;
      line-height: 70%;
      letter-spacing: 0.015em;
      background: linear-gradient(356.17deg, #000000 11.26%, rgba(0, 0, 0, 0) 131.17%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
    &-img{
      flex-shrink: 0;
      width: 49px;
    }
  }
  &__sale{
    position: absolute;
    top: 37px;
    right: 17px;
    font-weight: 300;
    font-size: 30px;
    line-height: 36px;
    letter-spacing: 0.015em;
    &:before{
      content: '';
      position: absolute;
      background: #2AB45A;
      transform: translate(-50%,-50%) rotate(-10.5deg);
      width: 100%;
      height: 2px;
      top: 50%;
      left: 50%;
    }
  }
  &__content{
    position: relative;
    z-index: 4;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__title{
    font-size: 30px;
    line-height: 120%;
    letter-spacing: 0.015em;
    color: #FFFFFF;
    margin-left: 30px;
    max-width: 336px;
    b{
      font-weight: 800;
    }
  }
  &__btn{
    width: 560px;
    height: 128px;
    max-width: 100%;
    margin-top: 22px;
  }
  &__img{
    position: absolute;
    max-height: calc(100% - 24px);
    top: 12px;
    right: 0;
    z-index: 3;
  }
}

.time{
  background: linear-gradient(90.01deg, #FEB2B2 0.01%, #FF7C90 87.32%);
  border-radius: 10px;
  padding: 12px 18px;
  letter-spacing: 0.015em;
  color: #FFFFFF;
  position: relative;
  &:after{
    content: '';
    position: absolute;
    width: calc(100% - 10px);
    height: calc(100% - 10px);
    left: 5px;
    top: 5px;
    pointer-events: none;
    border: 1px solid rgba(255, 255, 255, 0.25);
    border-radius: 5px;
  }
  &__title{
    font-size: 15px;
    line-height: 18px;
  }
  &__wrap{
    margin-top: 1px;
    display: flex;
    align-items: center;
  }
  &__num{
    font-weight: 800;
    font-size: 47px;
    line-height: 56px;
    color: #fff;
  }
  &__icon{
    width: 40px;
    flex-shrink: 0;
    margin-right: 15px;
    img{
      width: 100%;
      display: block;
    }
  }
}

.pay{
