      font-size: 350px;
      line-height: 70%;
      letter-spacing: 0.015em;
      background: linear-gradient(356.17deg, #000000 11.26%, rgba(0, 0, 0, 0) 131.17%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
    &-img{
      flex-shrink: 0;
