    }
    .fg{
      margin-bottom: 20px;
    }
  }
  &__btn{
    width: 100%;
    height: 128px;
  }
  &__check{
    &:not(:last-child){
      margin-bottom: 60px;
    }
  }
  &__subtitle{
    font-weight: 400;
    font-size: 60px;
    line-height: 50px;
    letter-spacing: 0.015em;
    color: #FF5F79;
    font-family: $font3;
  }
  &__desc{
    margin-top: 43px;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.015em;
    color: #696969;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
  }
}

.sale{
  margin-top: 35px;
  display: flex;
  &__wrap{
    background-image: url(../img/sale.webp);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: left center;
    min-height: 148px;
    padding: 22px 20px 30px 150px;
  }
  &__title{
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.015em;
  }
  &__num{
    font-weight: 800;
    font-size: 80px;
    line-height: 96px;
    letter-spacing: 0.015em;
    background: linear-gradient(357.92deg, #000000 -36.48%, rgba(0, 0, 0, 0) 133.59%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    margin-top: -10px;
  }
  .time{
    flex-grow: 1;
    max-width: 330px;
    padding: 25px 31px 15px;
    &__wrap{
      margin-top: 5px;
    }
    &__icon{
      width: 60px;
    }
    &__num{
      font-size: 65px;
      line-height: 78px;
    }
  }

}


@media screen and (max-width: 1150px){
  .pay{
    &__inner{
      grid-template-columns: 100%;
      margin-top: 0;
    }
    &__right{
      order: -1;
    }
    &__desc{
      max-width: 500px;
    }
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 1100px){
  .review{
    &__items{
      grid-template-columns: repeat(3,calc(33.33% - (40px / 3)));
    }
  }
}


