  }

  .conscious{
    padding: 25px 0 0;
    &__title{
      font-size: 120px;
    }
    &__desc{
      font-size: 20px;
      line-height: 19px;
    }
    &__img{
      margin-top: 31px;
    }
  }

  .pay{
    padding: 43px 0 28px;
    &__inner{
      margin-bottom: 27px;
    }
    &__desc{
      margin-top: 15px;
    }
    &__price{
      margin-top: 76px;
    }
    &__percent{
      max-width: 140px;
    }
    &__sum{
      &-num{
        font-size: 200px;
      }
      &-img{
        width: 43px;
      }
    }
    &__form{
      &-title{
        font-size: 13px;
        line-height: 16px;
      }
      .fg{
        margin-bottom: 10px;
      }
    }
    &__btn{
      height: 75px;
      font-size: 20px;
    }
    &__check{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
  }

  .sale{
    display: block;
    &__wrap{
      padding: 20px 20px 20px 145px;
      background-size: cover;
      border-radius: 5px 5px 0px 0px;
    }
    .time{
      max-width: 100%;
      width: 100%;
      border-radius: 0px 0px 5px 5px;
      &__wrap{
        margin-top: 15px;
      }
    }
  }
}


@media screen and (max-width: 600px){
  .review{
    &__items{
      grid-template-columns: 100%;
      gap: 23px;
    }
  }

  .faq{
    &__items{
      padding-top: 53px;
    }
  }
}
