.review-card{
  background: #F2F2F2;
  border-radius: 10px;
  position: relative;
  &:nth-child(1){
    .review-card__smile{
      top: 22px;
      left: -16px;
    }
  }
  &:nth-child(3){
    .review-card__smile{
      top: -18px;
      left: -13px;
    }
  }
  @media screen and (min-width: 1100px){
    &:nth-child(4){
      grid-column: 4/5;
      grid-row: 1/3;
      .review-card__smile{
        bottom: 20px;
        right: -21px;
      }
    }
    &:nth-child(5){
      grid-column: 1/-2;
      display: grid;
      grid-template-columns: .9fr 1fr 1fr;
      gap: 15px;
      .review-card__top{
        padding: 13px 0 13px 15px;
        &:before{
          top: -13px;
          height: 51px;
          background-image: url(../img/ellipse3.svg);
          background-position: left bottom;
        }
      }
      .review-card__desc{
        grid-column: 2/4;
        padding: 11px 15px 11px 0;
      }
      .review-card__smile{
        left: 25%;
        bottom: -23px;
      }
    }
  }
  &__top{
    overflow: hidden;
    position: relative;
    padding: 20px 14px 0;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 68px;
      width: 100%;
      border-radius: 10px 10px 0 0;
      background-image: url(../img/ellipse2.svg);
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: 100% auto;
    }
  }
  &__name{
    position: relative;
    z-index: 2;
    background: linear-gradient(177.01deg, rgba(255, 255, 255, 0.23) 2.47%, rgba(255, 255, 255, 0.33) 97.51%), linear-gradient(180deg, #2AB45A 0%, #78C252 100%);
    border-radius: 5px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 30px;
    letter-spacing: 0.015em;
    color: #FFFFFF;
    font-family: $font3;
    &:after{
      content: '';
      position: absolute;
      width: calc(100% - 8px);
      height: calc(100% - 8px);
      left: 4px;
      top: 4px;
      pointer-events: none;
      border: 1px solid rgba(255, 255, 255, 0.7);
      border-radius: 5px;
    }
  }
  &__desc{
    padding: 0 14px 11px;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.015em;
    color: #979797;
    margin-top: 5px;
  }
  &__smile{
    position: absolute;
    z-index: 3;
  }
}

.qa-card{
  max-width: 847px;
  margin: 0 auto;
  &:not(:last-child){
    margin-bottom: 26px;
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(90.01deg, #FEB2B2 0.01%, #FF7C90 87.32%);
    border-radius: 10px;
    min-height: 74px;
    padding: 16px 22px 16px 34px;
    position: relative;
    overflow: hidden;
    &:after{
      content: '';
      position: absolute;
      width: calc(100% - 16px);
      height: calc(100% - 18px);
      left: 8px;
      top: 9px;
      pointer-events: none;
      border: 1px solid rgba(255, 255, 255, 0.25);
      border-radius: 5px;
    }
    &:before{
      content: '';
      position: absolute;
      width: 205px;
      height: 205px;
      border-radius: 50%;
      background: linear-gradient(147.86deg, #FFB12F 17.46%, #FFCC77 73.66%);
      right: -70px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__title{
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.015em;
    color: #fff;
  }
  &__btn{
    position: relative;
    z-index: 2;
    flex-shrink: 0;
  }
  &__hidden{
    padding: 0 12px;
  }
  &__wrap{
    padding: 23px 15px 25px;
    background: #F2F2F2;
    border-radius: 10px;
    color: #696969;
  }
  &__desc{
    font-size: 17px;
    line-height: 21px;
    letter-spacing: 0.015em;
  }
}


@media screen and (max-width: 768px){
  .qa-card{
    &:not(:last-child){
      margin-bottom: 20px;
    }
    &__show{
      z-index: 2;
      padding: 6px 8px 6px 15px;
      min-height: 30px;
      &:after{
        width: calc(100% - 6px);
        height: calc(100% - 7px);
        left: 3px;
        top: 3.5px;
      }
      &:before{
        width: 80px;
        height: 80px;
        right: -22px;
      }
    }
    &__title{
      font-size: 10px;
      line-height: 12px;
    }
    &__btn{
      img{
        width: 16px;
      }
    }
    &__hidden{
      padding: 0 2px;
      border-radius: 10px;
      margin-top: -12px;
    }
    &__wrap{
      padding: 17px 14px 9px;

    }
    &__desc{
      font-size: 10px;
      line-height: 12px;
    }
  }
}
