@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
.slideToggle-block {
    &__hidden {
        height: 0;
        overflow: hidden;
        transition: all .425s;
    }
}

.fg {
    position: relative;
    margin: 0;
    input {
        border: 1px solid #C3C3C3;
        padding: 0 20px 0 100px;
        height: 90px;
        width: 100%;
        border-radius: 10px;
        font-weight: 300;
        font-size: 25px;
        background-color: transparent;
        color: #000;
        &::placeholder {
            color: #979797;
        }
        &:focus{
            border-color: #000;
        }
    }
    img{
        position: absolute;
        left: 31px;
        top: 50%;
        transform: translateY(-50%);
    }
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                border: none;
                background-image: url(../img/check.png);
                background-size: 100% auto;
                background-position: center;
                background-repeat: no-repeat;
            }
        }
    }
    label {
        padding-left: 50px;
        position: relative;
        font-weight: 300;
        font-size: 17px;
        line-height: 20px;
        letter-spacing: 0.015em;
        color: #696969;
        cursor: pointer;
        min-height: 30px;
        display: block;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            width: 30px;
            height: 30px;
            border: 1px solid #C3C3C3;
            border-radius: 5px;
            left: 0;
            top: 0;
            transition: all .100s;
        }
    }
    a{
        position: relative;
        z-index: 2;
        text-decoration: underline;
    }
}


@media screen and (max-width: 768px){
    .fg{
        input{
            height: 50px;
            padding-left: 60px;
            font-size: 20px;
        }
        img{
            width: 40px;
            left: 10px;
        }
    }

    .check{
        label{
            font-size: 14px;
            line-height: 17px;
        }
    }
}
