.footer{
  padding: 26px 0 58px;
  background: linear-gradient(90deg, rgba(243, 243, 243, 0.2) 6.21%, #F3F3F3 49.33%, rgba(243, 243, 243, 0.2) 85.75%);
  color: #979797;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title,
  &__co,
  &__mail{
    font-weight: 300;
    font-size: 17px;
    line-height: 20px;
    letter-spacing: 0.015em;
  }
  &__title{
    margin-bottom: 32px;
  }
  &__img{
    display: block;
    img{
      max-width: 100%;
    }
  }
  &__info{
    margin-top: 36px;
    padding: 17px 51px 17px 28px;
    background: #FFFFFF;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__mail{
    display: flex;
    align-items: center;
    img{
      margin-right: 20px;
      flex-shrink: 0;
    }
  }
  &__links{
    column-count: 2;
    column-gap: 70px;
    max-width: 550px;
    margin-top: 30px;
  }
  &__link{
    font-weight: 300;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.015em;
    text-decoration-line: underline;
    display: inline-block;
    width: 100%;
    &:not(:last-child){
      margin-bottom: 16px;
    }
    &:hover{
      text-decoration: none;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 24px 0 31px;
    &__top{
      display: grid;
      grid-template-columns: 100%;
      gap: 17px;
    }
    &__img{
      padding-right: 30px;
    }
    &__title{
      margin-bottom: 14px;
    }
    &__info{
      margin-top: 20px;
      padding: 12px 14px;
    }
    &__co{
      display: none;
    }
    &__mail{
      font-size: 14px;
      line-height: 17px;
    }
    &__links{
      margin-top: 20px;
      column-count: 1;
    }
  }
}
